class Category extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nowShow: 'c1',
            products: tabs[0].products
        }
    }

    handleTabClick(key) {
        let products = [];
        for(let item of tabs) {
            if(item.key === key) {
                products = item.products
            }
        }

        this.setState(() => ({
            nowShow: key,
            products
        }))
    }

    render() {
        return (
            <div className="category">
                <div className={"cate-img "+ this.state.nowShow}></div>
                <WhiteSpace size="lg" />
                <div className="tab-area">
                    <div className="tabs">
                        {
                            tabs.map((item) => {
                                return (
                                    <div 
                                        className={"tab-item " + (this.state.nowShow === item.key?'on':'')}
                                        key={`tab-${item.key}`}
                                        onClick={this.handleTabClick.bind(this,item.key)}
                                    >
                                        {item.title}
                                    </div>
                                )
                            })
                        }
                    </div>
                    <div className="tab-content">
                        <div className="prod-list">
                            {
                                this.state.products.map((prod) => {
                                    return (
                                        <Link to="/detail?" key={`prod-${prod.id}`}>
                                            <div className="prod-item">
                                                <div className="pi-content">
                                                    <img className="pi-img" alt="" src="https://photo.mac69.com/180720/EPS-180720_228/y1cm7b7qR4_small.jpg" />
                                                    <div className="pi-desc">{prod.describe}</div>
                                                </div>
                                                <div className="pi-price"><label>￥</label>{prod.price}</div>
                                            </div>
                                        </Link>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
                <WhiteSpace />
                <MenuBar selectedTab="category" />
            </div>
        )
    }
}




export default Category





