import React, { Fragment } from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from "react-router-dom";
import './menu.css'
import category from '../../icons/menu/category.svg';
import home from '../../icons/menu/home.svg';
import my from '../../icons/menu/my.svg';
import categoryOn from '../../icons/menu/category_on.svg';
import homeOn from '../../icons/menu/home_on.svg';
import myOn from '../../icons/menu/my_on.svg';
import { getCookie } from '../../utils'


class MenuBar extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			is_login: false
		}
	}
	
	componentDidMount() {
		let isLogin = getCookie('is_login');
		this.setState({
			is_login: isLogin == '1'
		})
	}
	

  handlePress(key) {
	console.log(key)
	console.log(this.state.is_login)
		if((key == "category" || key == "user") && !this.state.is_login) {
			window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5ec8e88c0880a364&redirect_uri=http%3a%2f%2fwww.xqdj.cn%2fapi%2fuser%2fauthorize&response_type=code&scope=snsapi_userinfo&state=http%3a%2f%2fwww.xqdj.cn/${key}#wechat_redirect`
		}
		this.props.history.push(`./${key}`)
  }


  render() {
    return (
      <Fragment>
          <div className="menu-bar">
            <TabBar
            unselectedTintColor="#949494"
            tintColor="#000000"
            barTintColor="white"
          >
            <TabBar.Item
              icon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${home}) center center /  21px 21px no-repeat` }}
                />
              }
              selectedIcon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${homeOn}) center center /  21px 21px no-repeat` }}
                />
              }
              title="首页"
              key="home"
              selected={this.props.selectedTab === 'home'}
              onPress={this.handlePress.bind(this,'')}
              data-seed="logId1"
            >
              
            </TabBar.Item>
            <TabBar.Item
              icon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${category}) center center /  21px 21px no-repeat` }}
                />
              }
              selectedIcon={
                <div style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${categoryOn}) center center /  21px 21px no-repeat` }}
                />
              }
              title="分类"
              key="category"
              selected={this.props.selectedTab === 'category'}
              onPress={this.handlePress.bind(this,'category')}
            >
              
            </TabBar.Item>
            <TabBar.Item
              icon={{ uri: my }}
              selectedIcon={{ uri: myOn }}
              title="我的订单"
              key="my"
              selected={this.props.selectedTab === 'my'}
              onPress={this.handlePress.bind(this,'user')}
            >
              
            </TabBar.Item>
          </TabBar>
        </div>
        <div className="mb-placeholder"></div>
      </Fragment>
      

    );
  }
}

export default withRouter(MenuBar);