http.getJspackage = async function() {
	let api = `${host}/api/user/jspackage`;
	let url = encodeURIComponent(window.location.href.split('#')[0]);
	axios.get(`${api}?url=${url}`).then((res) => {
		console.log('wx.config',res.data)
		wx.config({
			debug: false,
			appId: res.data.appId, // 必填，公众号的唯一标识
			timestamp: res.data.timestamp, // 必填，生成签名的时间戳
			nonceStr: res.data.nonceStr, // 必填，生成签名的随机串
			signature: res.data.signature,// 必填，签名,
			jsApiList: [
				"chooseWXPay",
				"openAddress"
			] // 必填，需要使用的JS接口列表
		})
	})
}

http.getIslogin = async function() {
	let api = `${host}/api/user/islogin`;
	axios.get(`${api}`).then((res) => {
		if(res.data.isLogin) {
			setCookie("is_login", "1", 356*24*60*60*1000);
		}else {
			setCookie("is_login", "0", 356*24*60*60*1000);
		}
	})
}









export default http;